[class*="__container"]{max-width:1170px;box-sizing:content-box;margin:0 auto;padding:0px 15px}.btn{display:flex;justify-content:center;align-items:center;min-height:50px;padding:5px 15px;border:1px solid #000;font-size:20px;background-color:#fff;transition:all 0.3s ease 0s}@media (any-hover: hover){.btn:hover{color:#fff;background-color:#000}}.btn:not(:last-child){margin-bottom:30px}.notes{padding:20px 0px}.notes__body{display:flex;flex-direction:column;align-items:center;width:100%}.notes__title{font-size:40px;font-weight:700;text-align:center}.notes__title:not(:last-child){margin-bottom:30px}.notes__list{display:flex;flex-direction:column;gap:20px;width:100%}.item-notes{border:1px solid #000}.item-notes__header{padding:15px;background-color:#000;display:flex;align-items:center;justify-content:space-between}.item-notes__icons{color:#fff;display:flex;align-items:center;gap:10px}@media (min-width: 767.98px){.item-notes__icons{gap:15px}}.item-notes__icons button{font-size:18px}@media (any-hover: hover){.item-notes__icons button{transition:all 0.3s ease 0s}}@media (any-hover: hover){.icons-button__del:hover{color:red}}@media (any-hover: hover){.icons-button__edit:hover{color:orange}}@media (any-hover: hover){.icons-button__check:hover{color:green}}.date-time-note{color:#fff;font-weight:700}@media (min-width: 767.98px){.date-time-note{font-size:18px}}.note-body{padding:10px}textarea{outline:none;min-height:150px;height:100%;width:100%;resize:vertical}
